<docs>
# KoddeinFormDatepicker
> Datepicker field

@author Kevin Bastien <kevin@kffein.com>
</docs>

<script lang="ts">
import { defineComponent, reactive, watch, toRefs, ref } from 'vue';
// import Datepicker from 'vue3-datepicker';
import Datepicker from '@vuepic/vue-datepicker';
import { store as i18nStore } from 'plugins/i18n/store';
import scrollStore from 'components/SmoothScroll/store';
import { fr, enCA } from 'date-fns/locale';
import '@vuepic/vue-datepicker/dist/main.css';

const en = enCA;

interface Data {
  internalValue?: Timestamp,
  active: boolean,
}

const i18nLocale:Koddein.Locale = i18nStore.locale as Koddein.Locale;

const KoddeinFormDatepicker = defineComponent({
  name: "KoddeinFormDatepicker",
  components: {
    Datepicker,
  },
  props: {
    id: {
      type: String,
      required: true,
    },
    placeholder: {
      type: String,
      default: '',
    },
    /** input value */
    value: {
      type: Number,
      default: undefined,
    },
    /** input error */
    error: {
      type: String,
      default: undefined,
    },
  },
  emits: ['inputChange', 'onFocus', 'onBlur'],
  setup(props, { emit }){
    const datepicker = ref<any>()
    const { y } = toRefs(scrollStore.state);

    const data:Data = reactive(
      {
        /** @param {any} internalValue - clone value
         * - because we must not change props
         * - in `watch` will sync value within internalValue
         */
        internalValue: undefined,
        active: false,
      }
    );

    /**
     * Watch value, on value update -> set internal value
     */
    watch(
      ()=>props.value,
      (value) => { 
        if (value) {
          data.internalValue = value;
        }
      },
      { immediate:true }
    )

    /**
     * Watch internal value, on value update -> send value to parent
     */
    watch(
      ()=>data.internalValue,
      (value) => { 
        if (value === props.value) return;
        emit('inputChange', value);
      },
      // { immediate:true }
    )

    watch(
      y,
      () => { 
        if (datepicker.value) {
          // Close the menu programmatically
          datepicker.value.closeMenu()
        }
      },
      // { immediate:true }
    )

    const focus = () => {
      emit('onFocus');
    }
    const blur = () => {
      emit('onBlur');
    }

    return {
      ...toRefs(data), //data
      datepicker,
      localeName: i18nLocale?.name || 'en',
      en,
      fr,
      isFocus: focus,
      isBlur: blur,
    }; 
  }
});
export default KoddeinFormDatepicker
</script>

<template>
  <div>
    {{ internalValue }}
    <Datepicker :id="id"
                ref="datepicker"
                v-model="internalValue"
                model-type="timestamp"
                :name="id"
                :locale="localeName"
                :format-locale="localeName ==='fr' ? fr : en"
                :placeholder="placeholder"
                :close-on-scroll="true"
                :class="['koddein-field-date', active ? '-active' : '', error ? '-error' : '']"
                maximum-view="day"
                format="yyyy-MM-dd"
                :month-change-on-scroll="false"
                :enable-time-picker="false"
                :auto-apply="true"
                @focus="isFocus"
                @blur="isBlur"
    />
  </div>
</template>

